/**
 * FormLanding.js
 * Wrapper that takes in the main landing form and
 * renders the 3 images with description, along
 * with the promo button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import _ from 'lodash';

import FooterPolicyPalImg from 'assets/images/Footer/footer-policy-pal.svg';
// import icons
import EmailIcon from 'assets/images/Icons/email.svg';
import PhoneIcon from 'assets/images/Icons/phone.svg';
import LocationIcon from 'assets/images/Icons/location.svg';
import FacebookIcon from 'assets/images/Business/Main/Facebook.svg';
import TwitterIcon from 'assets/images/Business/Main/Twitter.svg';
import InstagramIcon from 'assets/images/Business/Main/Instagram.svg';
import LinkedInIcon from 'assets/images/Business/Main/LinkedIn.svg';
import AppStoreBadge from 'assets/images/app-store-badge.png';
import PlayStoreBadge from 'assets/images/google-play-badge.png';

// import Like Facebook button
import FacebookLike from '../../Templates/FacebookLike/FacebookLike';

import PhoneButton from './PhoneButton';

import './FormLandingFooter.scss';

const contactPPS = [
  { img: EmailIcon, href: 'mailto:hello@policypal.com', text: 'hello@policypal.com' },
  { img: PhoneIcon, href: 'tel:+6591843163', text: '+65 3163 9184' },
  {
    img: LocationIcon,
    href: 'https://goo.gl/maps/2vf8g6LCQrt',
    text: 'PolicyPal\n38 Eng Hoon Street\nSingapore 169783',
    rel: 'noopener noreferrer',
  },
];

const sosMedPPS = [
  { img: FacebookIcon, href: 'https://www.facebook.com/hipolicypal' },
  { img: TwitterIcon, href: 'https://twitter.com/hipolicypal' },
  { img: InstagramIcon, href: 'https://www.instagram.com/policypal/' },
  { img: LinkedInIcon, href: 'https://www.linkedin.com/company/policypal/' },
];

const contentPPS = [{
  products: [
    { href: '/travel', text: 'Travel' },
    { href: '/endowment', text: 'Endowment' },
    { href: '/motor', text: 'Motor' },
    { href: '/retirement', text: 'Retirement' },
  ],
  products1: [
    { href: '/life', text: 'Life' },
    { href: '/pet', text: 'Pet' },
    { href: '/personal-accident', text: 'Personal Accident' },
    { href: '/business', text: 'Business Insurance' },
  ],
}, {
  company: [
    { href: '/claims', text: 'Claim' },
    { href: '/partnership', text: 'Partnership' },
    { href: '/rewards', text: 'Rewards' },
    { href: '/about', text: 'About Us' },
    { href: '/career', text: 'Career' },
    { href: '/faq', text: 'FAQ' },
    { href: 'https://blog.policypal.com/', text: 'Blog', type: 'external' },
    
  ],
  resources: [
    { href: '/tos', text: 'Terms of Service' },
    { href: '/privacypolicy', text: 'Privacy Policy' },
    { href: '/fair-dealing-statement', text: 'Fair Dealing Statement' },
    { href: '/security', text: 'Security' },
    // { href: '/press', text: 'Press' },
  ],
}];

class FormLandingFooter extends Component {
  renderDownloadNow(extraClassName) {
    return (
      <Row noGutters className={`download-now ${extraClassName}`}>
        <Col xs={12}>
          <h5>MOBILE APP</h5>
        </Col>
        <Col xs={12}>
          <Row noGutters className="download-links">
            <a href="https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8" target="__blank">
              <img className="download-img" src={AppStoreBadge} alt="appstore.jpg" />
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&amp;hl=en" target="__blank">
              <img className="download-img" src={PlayStoreBadge} alt="playstore.jpg" />
            </a>
          </Row>
        </Col>
      </Row>
    );
  }
  renderLeftSegment() {
    return (
      <Col xs={12} md={4}>
        { contactPPS.map(contact => (
          <Row key={contact.img} noGutters className="contact-info">
            <img src={contact.img} className="icon" alt={`${contact.img}.png`} />
            <a href={contact.href} rel={contact.rel || ''} target={contact.rel ? '_blank' : ''}>
              <p className="margin-left-10 pre-line">{contact.text}</p>
            </a>
          </Row>
        ))}
        <Row noGutters className="social-media-group">
          { sosMedPPS.map(sosMed => (
            <div key={sosMed.img} className="social-media-item">
              <a href={sosMed.href} target="_blank" rel="noopener noreferrer">
                <img src={sosMed.img} className="social-media-icons" alt={`${sosMed.img}.png`} />
              </a>
            </div>
          ))}
        </Row>

        <Row className="facebook-like" noGutters>
          <FacebookLike />
        </Row>

        { this.renderDownloadNow('d-none d-md-block') }
      </Col>
    );
  }

  renderProductSegment() {
    const { history } = this.props;

    return (
      <div>
        <h5 className="products-header">PRODUCTS</h5>
        <Col sm={12} className="col-display">
          { _.map(contentPPS[0], (v, idx) => (
            <Col key={`col-${idx}`} className="content-item" xs={12} sm={6}>
              { _.map(v, item => (
                <button key={item.text} className="link" onClick={() => history.push(item.href)}>
                  <p className="content-label">{item.text}</p>
                </button>
              ))}
            </Col>
        ))}
        </Col>
      </div>
    );
  }

  renderSegment(id = 'company') {
    const { history } = this.props;

    return (
      <Col sm={6}>
        <h5>{id.toUpperCase()}</h5>
        <div className="content-item">
          { _.map(contentPPS[1][id], (item) => {
            if (item.type && item.type === 'external') {
              return (
                <a key={item.text} href={item.href} target="_blank" rel="noopener noreferrer">
                  <p className="content-label">{item.text}</p>
                </a>
              );
            }
            return (
              <button key={item.text} className="link" onClick={() => history.push(item.href)}>
                <p className="content-label">{item.text}</p>
              </button>
            );
          })}
        </div>
      </Col>
    );
  }

  renderContents() {
    const { big, usingSticky, noPolicyPal } = this.props;
    const bigStyle = big ? 'big-footer' : '';
    const stickyStyle = usingSticky ? 'sticky-padding' : '';

    return (
      <div className={`${stickyStyle} form-landing-footer-container`}>
        <Row className={`footer ${bigStyle} full`} noGutters>
          { this.renderLeftSegment() }
          <Col className="col-display d-none d-md-flex" xs={6} md={4}>
            { this.renderProductSegment() }
          </Col>
          <Col className="col-display d-none d-md-flex" xs={6} md={4}>
            { this.renderSegment('company') }
            { this.renderSegment('resources') }
          </Col>
          <Col className="copyright d-none d-md-flex" xs={12}>
            <h6>&copy; PolicyPal 2019</h6>
          </Col>

          <Col className="col-display d-flex d-md-none" xs={6} md={4}>
            { this.renderDownloadNow('d-flex d-md-none') }
            { this.renderSegment('company') }
            <div className="copyright-mobile">
              <h6>&copy; PolicyPal 2019</h6>
            </div>
          </Col>
          <Col className="col-display d-flex d-md-none" xs={6} md={4}>
            { this.renderProductSegment() }
            { this.renderSegment('resources') }
          </Col>
        </Row>
        {
          !noPolicyPal ?
            <Row className="d-none d-lg-flex form-landing-footer-policypal-full" noGutters>
              <Col xs={12}>
                <img className={bigStyle} src={FooterPolicyPalImg} alt="policypal.svg" />
              </Col>
            </Row>
          : null
        }
      </div>
    );
  }

  render() {
    const {
      big,
      noPolicyPal,
      mode,
      usingSticky,
    } = this.props;

    const bigStyle = big ? 'big-footer' : '';
    const stickyStyle = usingSticky ? 'sticky-padding' : '';

    if (mode === 'full') {
      return this.renderContents();
    }

    return (
      <Fragment>
        <Row className={`${stickyStyle} form-landing-footer-container`} noGutters>
          <Col xs={12} className={`footer ${bigStyle}`}>
            <PhoneButton />
            <div className="small-container">
              <h3>Customer service hours</h3>
              <h6>10am - 7pm</h6>
              <h6>Mondays to Fridays</h6>
              <h6>(excluding public holidays)</h6>
            </div>
          </Col>
        </Row>
        {
          !noPolicyPal ?
            <Row className="form-landing-footer-policypal" noGutters>
              <Col xs={12}>
                <img className={bigStyle} src={FooterPolicyPalImg} alt="policypal.svg" />
              </Col>
            </Row>
          : null
        }
      </Fragment>
    );
  }
}

FormLandingFooter.defaultProps = {
  big: false,
  noPolicyPal: false,
  mode: 'short',
  usingSticky: false,
};

FormLandingFooter.propTypes = {
  history: PropTypes.object.isRequired,
  big: PropTypes.bool,
  noPolicyPal: PropTypes.bool,
  mode: PropTypes.string,
  usingSticky: PropTypes.bool,
};

export default withRouter(connect(null, null)(FormLandingFooter));
